.listContainer {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0.5rem;
    margin: 0.5rem;
}

.listContainer input {
    text-align: right;
    padding: 0.5rem;
    margin: 0.5rem;
    width: 2rem;
    height: 2rem;
}

.listContainer li {
    padding: 0.5rem;
    margin: 0.5rem;
    font-size: xx-large;
}

.listContainer button {
    padding: 0.5rem;
    margin: 0.5rem;
    text-align: left;
    border-radius: 0.5rem;
}

.listItemDone {
    text-decoration: line-through black;
}